.c-section-header {

}

.c-section-header__title {
  text-align: center;
  font-weight: 400;
  font-size: rem(20);
  position: relative;

  @include mq('tab') {
    font-size: rem(30);
  }
}

.c-section-header__subtitle {
  display: inline-block;
  text-align: center;
  color: #F9F871;
  opacity: .27;
  font-size: rem(50);
  font-family: $font-family-sub;
  content:"";
  position: absolute;
  top: rem(-50);
  left: 50%;
  width: 100%;
  transform: translateX(-50%);
  height: rem(72);
  letter-spacing: rem(2);

  @include mq('tab') {
    font-size: rem(110);
    left: 15%;
    top: rem(-43);
  }
  
}